// @ts-nocheck
import { registerBlockType } from "@wordpress/blocks";
import { TextareaControl } from "@wordpress/components";
import App from "./App";
registerBlockType("uvic-captions-player/embed-player", {
    title: "UVic Captions Player",
    icon: "video-alt",
    category: "media",
    attributes: {
        playerEmbed: {
            type: "string"
        }
    },
    edit: onEditBlock,
    save: onSaveBlock
});
function onEditBlock(props) {
    var attributes = props.attributes, setAttributes = props.setAttributes;
    var updateFieldValue = function (val) {
        setAttributes({ playerEmbed: val });
    };
    return (React.createElement(TextareaControl, { label: "Embed UVic Player", value: attributes.playerEmbed, onChange: updateFieldValue }));
}
function onSaveBlock(props) {
    var playerEmbed = props.attributes.playerEmbed;
    return React.createElement(App, { playerEmbed: playerEmbed });
}
